<template>
  <div class="container has-text-centered">
    <div class="column is-4 is-offset-4">
      <h3 class="title has-text-grey">新駅名ジェネレータ</h3>
      <h4 class="subtitle has-text-grey">あなたの新駅名は・・・</h4>
      <div class="box">
        <div class="words">
          <span :class="col1.style">{{ col1.word }}</span>
          <span :class="col2.style">{{ col2.word }}</span>
          <span :class="col3.style">{{ col3.word }}</span>
        </div>
      </div>
      <div class="btns">
        <button class="stop btn" v-on:click="onSelected('col1')">1</button>
        <button class="stop btn" v-on:click="onSelected('col2')">2</button>
        <button class="stop btn" v-on:click="onSelected('col3')">3</button>
      </div>
      <div v-show="twitter.visible" v-on:click="reload()" class="button is-rounded">もう一駅！</div>

      <a
        v-show="twitter.visible"
        :href="twitter.URL"
        class="twitter button is-info is-rounded"
        data-lang="ja"
      >
        <span class="icon">
          <i class="fab fa-twitter"></i>
        </span>
        <span>Tweet！</span>
      </a>
    </div>
    <footer>
      作った人:
      <a href="https://twitter.com/418iamteapot">@418iamteaport</a>
    </footer>
  </div>
</template>

<script>
export default {
  name: 'Roulette',
  data () {
    return {
      col1: {
        word: 'hoge',
        state: true,
        style: 'has-text-grey-lighter'
      },
      col2: {
        word: 'hoge',
        state: true,
        style: 'has-text-grey-lighter'
      },
      col3: {
        word: 'hoge',
        state: true,
        style: 'has-text-grey-lighter'
      },
      roulette: {
        num: null
      },
      twitter: {
        URL: '',
        visible: false
      },
      kanjiWordList: [
        '大',
        '崎',
        '品',
        '川',
        '田',
        '町',
        '浜',
        '松',
        '町',
        '新',
        '橋',
        '有',
        '楽',
        '町',
        '東',
        '京',
        '神',
        '田',
        '秋',
        '葉',
        '原',
        '御',
        '徒',
        '町',
        '上',
        '野',
        '鶯',
        '谷',
        '日',
        '暮',
        '里',
        '西',
        '日',
        '暮',
        '田',
        '端',
        '駒',
        '込',
        '巣',
        '鴨',
        '大',
        '塚',
        '池',
        '袋',
        '目',
        '白',
        '高',
        '田',
        '馬',
        '新',
        '大',
        '久',
        '新',
        '宿',
        '代',
        '々',
        '木',
        '原',
        '宿',
        '渋',
        '谷',
        '恵',
        '比',
        '寿',
        '目',
        '黒',
        '五',
        '反',
        '田'
      ],
      katakanaWordList: [
        'サディスティック',
        'ミカバンド',
        'エクスプレス',
        'ゲートウェイ',
        'エリートスクール',
        'パークランド',
        'ファッションタウン',
        'ガーデンパーク',
        'もふもふランド',
        'スクェア',
        'ランドマークタウン',
        'セントラルパーク',
        'アルプス',
        'サンクチュアリ',
        'ガソリンスタンド',
        'セントラルマルシェ',
        'エアポート',
        'ヤングシティ',
        'ホスピタル',
        'ストリート',
        'ランランビレッジ',
        'ポート',
        'フラワータウン',
        'フラワービレッジ',
        'ギルティプリズン',
        'セントラルキャッスル',
        'ビューティーパレス',
        'アカデミー',
        'ビッグスタチュー',
        'ポジティブテリトリー',
        'ヒートアイランド'
      ]
    }
  },
  created () {
    this.start()
  },
  methods: {
    start: function () {
      this.roulette.num = setInterval(() => {
        if (this.col1.state === true) {
          this.col1.word = this.selectWord(this.kanjiWordList)
        }
      }, (1000 / 24))
      this.roulette.num = setInterval(() => {
        if (this.col2.state === true) {
          this.col2.word = this.selectWord(this.kanjiWordList)
        }
      }, (1000 / 24))
      this.roulette.num = setInterval(() => {
        if (this.col3.state === true) {
          this.col3.word = this.selectWord(this.katakanaWordList)
        }
      }, (1000 / 24))
    },
    selectWord: function (wordList) {
      return wordList[Math.floor(Math.random() * wordList.length)]
    },
    onSelected: function (col) {
      switch (col) {
        case 'col1':
          this.col1.state = false
          this.col1.style = 'has-text-danger word'
          break
        case 'col2':
          this.col2.state = false
          this.col2.style = 'has-text-danger word'
          break
        case 'col3':
          this.col3.state = false
          this.col3.style = 'has-text-danger word'
          break
      }
      this.createTwitter()
    },
    reload: function () {
      location.reload()
    },
    createTwitter: function () {
      if (this.col1.state === false && this.col2.state === false && this.col3.state === false) {
        this.twitter.visible = true
        this.twitter.URL = 'https://twitter.com/intent/tweet?text=' + '私が考えた新しい駅名は 「' + this.col1.word + this.col2.word + this.col3.word + '」です' + '&hashtags=' + '新駅名ジェネレータ'
      }
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
p,
h1,
h2,
h3 {
  font-family: "Avenir", Helvetica, Arial, sans-serif;
  font-weight: normal;
}
ul {
  list-style-type: none;
  padding: 0;
}
li {
  display: inline-block;
  margin: 0 10px;
}
a {
  color: #42b983;
}

footer {
  position: fixed;
  bottom: 0;
  width: 100%;
  height: 100px;
}

.subtitle {
  margin-top: 30px;
  margin-bottom: 10px;
}

.btn {
  font-size: 20px;
  display: inline-block;
  text-decoration: none;
  background: #ff8181;
  color: #fff;
  width: 60px;
  height: 60px;
  line-height: 60px;
  border-radius: 50%;
  text-align: center;
  font-weight: bold;
  vertical-align: middle;
  overflow: hidden;
  box-shadow: 0px 2px 2px rgba(0, 0, 0, 0.29);
  border-bottom: solid 3px #bd6565;
  transition: 0.4s;
}

.btn:active {
  -ms-transform: translateY(2px);
  -webkit-transform: translateY(2px);
  transform: translateY(2px);
  box-shadow: 0 0 1px rgba(0, 0, 0, 0.15);
  border-bottom: none;
}

.words,
.btns {
  display: flex;
  justify-content: center;
  align-items: center;
}

.tw_followb {
  text-decoration: none;
  background-image: linear-gradient(#fff, #f5f8fa);
  border: 1px solid #e6ecf0;
  border-radius: 4px;
  color: #393e42;
  cursor: pointer;
  display: inline-block;
  font-size: 14px;
  font-weight: bold;
  padding: 8px 14px;
  position: relative;
}

.tw_followb .fa-user-plus {
  color: #1da1f3;
}

.tw_followb:hover {
  background-color: #e6ecf0;
  background-image: linear-gradient(#fff, #e6ecf0);
  border-color: #e6ecf0;
}

.btn {
  margin-right: 10px;
  margin-left: 10px;
  margin-bottom: 40px;
}

.twitter {
  margin-bottom: 40px;
}

.word {
  font-size: 24px;
  font-weight: bold;
}
</style>



